module.exports = "import React, { Component } from 'react';\nimport styled from 'styled-components';\nimport Logo from './logo';\n\nconst Container = styled.main`\n  display: flex;\n  flex-direction: column;\n  min-height: 100%;\n  width: 100%;\n  background-color: #f6f9fc;\n`;\n\nconst Header = styled.header`\n  display: flex;\n  flex-direction: column;\n  height: 40%;\n  min-height: 250px;\n  width: 100%;\n  position: relative;\n  justify-content: center;\n`;\n\nconst LogoContainer = styled.div`\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  height: 60px;\n  width: 60px;\n  border-radius: 60px;\n  background-color: white;\n  box-shadow: 0 1px 6px rgba(0, 0, 0, 0.1);\n  position: absolute;\n  top: 1rem;\n  left: 1rem;\n  z-index: 2;\n`;\n\nconst Stripes = styled.div`\n  position: absolute;\n  top: 0;\n  right: 0;\n  bottom: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n  overflow: hidden;\n  transform: skewY(-8deg);\n  transform-origin: 0;\n  background: linear-gradient(-150deg, #25b57f 0%, #acf0b5 70%);\n`;\n\nconst Stripe = styled.div`\n  height: 10vh;\n  overflow: hidden;\n  transform: skewY(-8deg);\n  transform-origin: 0;\n  background: linear-gradient(-150deg, rgba(255, 255, 255, 0) 40%, #ddecf7 70%);\n`;\n\nconst StripeBottom = styled.div`\n  position: absolute;\n  bottom: 0;\n  left: 0;\n  right: 0;\n  height: 35%;\n  overflow: hidden;\n  background: linear-gradient(150deg, rgba(172, 240, 181, 0) 40%, #acf0b5 70%);\n`;\n\nconst TitleContainer = styled.div`padding: 1rem;`;\n\nconst Title = styled.h1`\n  color: white;\n  margin: 0;\n  padding: 0;\n  position: relative;\n  font-size: 1.5rem;\n  font-weight: 100;\n`;\n\nconst SubTitle = styled(Title)`\n\tcolor: #025450;\n\tmargin-top: 0.5rem;\n`;\n\nconst Form = styled.form`\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  justify-content: center;\n  width: 100%;\n  padding: 2rem;\n  box-sizing: border-box;\n`;\n\nconst Input = styled.input`\n  display: block;\n  width: 100%;\n  margin-bottom: 1rem;\n  padding: 1.25rem 1rem;\n  box-sizing: border-box;\n  border-radius: 0.25rem;\n  border: 1px solid transparent;\n  box-shadow: 0 1px 6px rgba(0, 0, 0, 0.1);\n  &:focus {\n    border-color: #6772e5;\n    outline: none;\n    box-shadow: 0 1px 6px rgba(103, 114, 229, 0.5);\n  }\n`;\n\nconst SubmitButton = styled.button.attrs({\n  type: 'submit'\n})`\n  display: block;\n  background-color: ${props => (props.disabled ? '#BBB' : '#6772e5')};\n  color: white;\n  border: none;\n  width: 100%;\n  padding: 1.25rem 1rem;\n  box-sizing: border-box;\n  border-radius: 0.25rem;\n  text-transform: uppercase;\n  box-shadow: 0 1px 6px rgba(0, 0, 0, 0.1);\n  margin-top: 1rem;\n`;\n\nexport default class Login extends Component {\n  constructor(props) {\n    super(props);\n    this.state = {\n      email: '',\n      phoneNumber: '',\n      fields: ['email', 'phoneNumber'],\n      valid: false\n    };\n  }\n\n  handleInputChange(prop) {\n    return ev => {\n      const { value } = ev.target;\n      this.setState({\n        [prop]: value,\n        valid:\n          value.length > 0 &&\n          this.state.fields.every(field => this.state[field].length > 0)\n      });\n    };\n  }\n\n  render() {\n    return (\n      <Container>\n        <Header>\n          <LogoContainer>\n            <Logo color=\"#25b57f\" size={32} />\n          </LogoContainer>\n          <Stripes>\n            <StripeBottom />\n          </Stripes>\n          <TitleContainer>\n            <Title>Set up your payments</Title>\n            <SubTitle>Rocketship, Inc.</SubTitle>\n          </TitleContainer>\n        </Header>\n        <Stripe />\n        <Form onSubmit={ev => ev.preventDefault()}>\n          <Input\n            type=\"text\"\n            placeholder=\"Email\"\n            onChange={this.handleInputChange('email')}\n          />\n          <Input\n            type=\"text\"\n            placeholder=\"Phone number\"\n            onChange={this.handleInputChange('phoneNumber')}\n          />\n          <SubmitButton disabled={!this.state.valid}>\n            Submit\n          </SubmitButton>\n        </Form>\n      </Container>\n    );\n  }\n}\n"


