module.exports = "import React, { Component } from 'react';\nimport cxs from 'cxs/component';\nimport Logo from './logo';\n\nconst Container = cxs('main')({\n  display: 'flex',\n  flexDirection: 'column',\n  minHeight: '100%',\n  width: '100%',\n  backgroundColor: '#f6f9fc'\n});\n\nconst Header = cxs('header')({\n  display: 'flex',\n  flexDirection: 'column',\n  height: '40%',\n  minHeight: 250,\n  width: '100%',\n  position: 'relative',\n  justifyContent: 'center'\n});\n\nconst LogoContainer = cxs('div')({\n  display: 'flex',\n  alignItems: 'center',\n  justifyContent: 'center',\n  height: 60,\n  width: 60,\n  borderRadius: 60,\n  backgroundColor: 'white',\n  boxShadow: '0 1px 6px rgba(0, 0, 0, 0.1)',\n  position: 'absolute',\n  top: '1rem',\n  left: '1rem',\n  zIndex: '2'\n});\n\nconst Stripes = cxs('div')({\n  position: 'absolute',\n  top: 0,\n  right: 0,\n  bottom: 0,\n  left: 0,\n  width: '100%',\n  height: '100%',\n  overflow: 'hidden',\n  transform: 'skewY(-8deg)',\n  transformOrigin: 0,\n  background: 'linear-gradient(-150deg, #25b57f 0%, #acf0b5 70%)'\n});\n\nconst Stripe = cxs('div')({\n  height: '10vh',\n  overflow: 'hidden',\n  transform: 'skewY(-8deg)',\n  transformOrigin: 0,\n  background:\n    'linear-gradient(-150deg, rgba(255, 255, 255, 0) 40%, #ddecf7 70%)'\n});\n\nconst StripeBottom = cxs('div')({\n  position: 'absolute',\n  bottom: 0,\n  left: 0,\n  right: 0,\n  height: '35%',\n  overflow: 'hidden',\n  background: 'linear-gradient(150deg, rgba(172, 240, 181, 0) 40%, #acf0b5 70%)'\n});\n\nconst TitleContainer = cxs('div')({\n  padding: '1rem'\n});\n\nconst Title = cxs('h1')({\n  color: 'white',\n  margin: 0,\n  padding: 0,\n  position: 'relative',\n  fontSize: '1.5rem',\n  fontWeight: '100'\n});\n\nconst SubTitle = cxs(Title)({\n  color: '#025450',\n  marginTop: '0.5rem'\n});\n\nconst Form = cxs('form')({\n  display: 'flex',\n  flexDirection: 'column',\n  alignItems: 'center',\n  justifyContent: 'center',\n  width: '100%',\n  padding: '2rem',\n  boxSizing: 'border-box'\n});\n\nconst Input = cxs('input')({\n  display: 'block',\n  width: '100%',\n  marginBottom: '1rem',\n  padding: '1.25rem 1rem',\n  boxSizing: 'border-box',\n  borderRadius: '0.25rem',\n  border: '1px solid transparent',\n  boxShadow: '0 1px 6px rgba(0, 0, 0, 0.1)',\n  ':focus': {\n    borderColor: '#6772e5',\n    outline: 'none',\n    boxShadow: '0 1px 6px rgba(103, 114, 229, 0.5)'\n  }\n});\n\nconst SubmitButton = cxs('button')(props => ({\n  display: 'block',\n  backgroundColor: props.disabled ? '#BBB' : '#6772e5',\n  color: 'white',\n  border: 'none',\n  width: '100%',\n  padding: '1.25rem 1rem',\n  boxSizing: 'border-box',\n  borderRadius: '0.25rem',\n  textTransform: 'uppercase',\n  boxShadow: '0 1px 6px rgba(0, 0, 0, 0.1)',\n  marginTop: '1rem'\n}));\n\nSubmitButton.defaultProps = {\n  type: 'submit'\n};\n\nexport default class Login extends Component {\n  constructor(props) {\n    super(props);\n    this.state = {\n      email: '',\n      phoneNumber: '',\n      fields: ['email', 'phoneNumber'],\n      valid: false\n    };\n  }\n\n  handleInputChange(prop) {\n    return ev => {\n      const { value } = ev.target;\n      this.setState({\n        [prop]: value,\n        valid:\n          value.length > 0 &&\n          this.state.fields.every(field => this.state[field].length > 0)\n      });\n    };\n  }\n\n  render() {\n    return (\n      <Container>\n        <Header>\n          <LogoContainer>\n            <Logo color=\"#25b57f\" size={32} />\n          </LogoContainer>\n          <Stripes>\n            <StripeBottom />\n          </Stripes>\n          <TitleContainer>\n            <Title>Set up your payments</Title>\n            <SubTitle>Rocketship, Inc.</SubTitle>\n          </TitleContainer>\n        </Header>\n        <Stripe />\n        <Form onSubmit={ev => ev.preventDefault()}>\n          <Input\n            type=\"text\"\n            placeholder=\"Email\"\n            onChange={this.handleInputChange('email')}\n          />\n          <Input\n            type=\"text\"\n            placeholder=\"Phone number\"\n            onChange={this.handleInputChange('phoneNumber')}\n          />\n          <SubmitButton disabled={!this.state.valid}>\n            Submit\n          </SubmitButton>\n        </Form>\n      </Container>\n    );\n  }\n}\n"


